/*----------------------------------*\
    Footer
\*----------------------------------*/
.footer {
  &__links{
    border-left: solid 3px $color-yellow;
    padding-left: 35px;
    a{
      font-size: 22px;
      @include transition();
      &:hover{
        color: $color-yellow;
      }
    }
    @media #{$sm-down}{
      border: 0;
      padding-left: 15px;
      padding-bottom: 30px;
    }
  }

  &__main{
    @media #{$xs-down}{
      flex-direction: column-reverse;
      -webkit-flex-direction: column-reverse;
    }
  }
  &__copy{
    border-top: solid 1px #d5d5d5;
  }

}
