/*----------------------------------*\
  	Variables
\*----------------------------------*/


/*----------------------------------*\
    font variables
\*----------------------------------*/


/* importing font */

@font-face {
  font-family: ProximaNova;
  src: url(../fonts/ProximaNova-Regular.otf);
  font-weight: 400;
}

@font-face {
  font-family: ProximaNova;
  src: url(../fonts/ProximaNova-SemiBold.otf);
  font-weight: 500;
}
@font-face {
  font-family: ProximaNova;
  src: url(../fonts/ProximaNova-Bold.otf);
  font-weight: 700;
}


/* Font Families */
$font-primary: "ProximaNova";
$font-secondary: "";
$font-tertiary: "";

/* Font Weights */
$light: 300;
$normal: 400;
$bold: 700;

/*----------------------------------*\
    color variables
\*----------------------------------*/

/* color variables */
$color-white: #fff;
$color-black: #000;
$color-yellow: #fcd505;

/* colors you want to include in text and background */
$colors: (

        'white': $color-white,
        'yellow': $color-yellow,

);


/* percentage of darkening color */
$darken: 5%;
/* percentage of lightening color */
$lighten: 8%;



/*----------------------------------*\
    spacings variables
\*----------------------------------*/

/************\
    header
\************/
$header-breakpoint: 992px;
$header-down: 'only screen and (max-width : #{$header-breakpoint - 1px})';
$header-up: 'only screen and (min-width : #{$header-breakpoint})';

/*************\
  breakpoints
\*************/

/* default */
$d-min: 0px;
$d-max: 319px;

/* xs2 */
$xs2-min: 320px;
$xs2-max: 479px;

/* xs */
$xs-min: 480px;
$xs-max: 767px;

/* sm */
$sm-min: 768px;
$sm-max: 991px;

/* md */
$md-min: 992px;
$md-max: 1199px;

/* lg */
$lg-min: 1200px;
$lg-max: 1439px;

/* lg2 */
$extra-min: 1440px;

/******************\
  @media variables
\******************/

/* 0 px */
$default: 'only screen and (min-width : #{$d-min})';

/* 320 px */
$xs3-down: 'only screen and (max-width : #{$d-max})'; // 320 and lower
$xs2-up: 'only screen and (min-width : #{$xs2-min})'; // 320 and higher

/* 480 px */
$xs2-down: 'only screen and (max-width : #{$xs2-max})'; // 480 and lower
$xs-up: 'only screen and (min-width : #{$xs-min})'; // 480 and higher

/* 768 px */
$xs-down: 'only screen and (max-width : #{$xs-max})'; // 768 and lower
$sm-up: 'only screen and (min-width : #{$sm-min})'; // 768 and higher

/* 992 px */
$sm-down: 'only screen and (max-width : #{$sm-max})'; // 992 and lower
$md-up: 'only screen and (min-width : #{$md-min})'; // 992 and higher

/* 1200 px */
$md-down: 'only screen and (max-width : #{$md-max})'; // 1200 and lower
$lg-up: 'only screen and (min-width : #{$lg-min})'; // 1200 and higher

/* 1440 px */
$lg-down: 'only screen and (max-width : #{$lg-max})'; // 1440 and lower
$extra-up: 'only screen and (min-width : #{$extra-min})'; // 1440 and higher



/* only  */
$xs-only: 'only screen and (min-width : #{$d-min}) and (max-width : #{$xs-max})';
$sm-only: 'only screen and (min-width : #{$sm-min}) and (max-width : #{$sm-max})';
$md-only: 'only screen and (min-width : #{$md-min}) and (max-width : #{$md-max})';
$lg-only: 'only screen and (min-width : #{$lg-min}) and (max-width : #{$lg-max})';




/*----------------------------------*\
    grid variables
\*----------------------------------*/


/* number of columns */
$grid: 12;

/* column padding */
$padding: 15px;

/* container spacing */
$container-space: 0px;

/* breakpoints */
$breakpoints: (
        'xs': $default,
        'sm': $sm-up,
        'md': $md-up,
        'lg': $lg-up
);

/*----------------------------------*\
    spacings variables
\*----------------------------------*/


/* sizes of padding and margin */
$sizes: 0, 10, 15, 20, 30, 45, 60, 90, 150;

/* prefixes */
$contains: (
        'pt': padding-top,
        'pb': padding-bottom,
        'pr': padding-right,
        'pl': padding-left,
        'mt': margin-top,
        'mb': margin-bottom,
        'mr': margin-right,
        'ml': margin-left
);

/* responsive spacings */
$spacing-md: 'false';
$spacing-sm: 'false';
$spacing-xs: 'false';


/*----------------------------------*\
    headings variables
\*----------------------------------*/

/*
$md-down = less than 1200px
$sm-down = less than 992px
$xs-down = less than 768px
 */

$headings: (
        'h1, .h1':
        (       /* h1 */
                $default: 80px,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        ),
        'h2, .h2':
        (       /* h2 */

                $default: 60px,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        ),
        'h3, .h3':
        (       /* h3 */

                $default: 40px,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        ),
        'h4, .h4':
        (       /* h4 */

                $default: 28px,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        ),
        'h5, .h5':
        (       /* h5 */

                $default: inherit,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        ),
        'h6, .h6':
        (       /* h6 */

                $default: inherit,
          //$md-down: px,
          //$sm-down: px,
          //s$xs-down: px

        )
);


/*----------------------------------*\
    mixins
\*----------------------------------*/


/* Transitions */
@mixin transition($args: 200ms) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

/* Transforms */
@mixin transform($args) {
  -webkit-transform: $args;
  -moz-transform: $args;
  -ms-transform: $args;
  -o-transform: $args;
  transform: $args;
}

/* Box-shaddow */
@mixin box-shadow($args) {
  -webkit-box-shadow: $args;
  -moz-box-shadow: $args;
  -ms-box-shadow: $args;
  -o-box-shadow: $args;
  box-shadow: $args;
}


