/* Checkbox properties */
input {
  outline: none; }

textarea {
  resize: vertical; }

.material {
  position: relative;
  padding: 0;
  border: none;
  overflow: visible; }
  .material textarea {
    box-sizing: border-box;
    width: 100%;
    padding: 12px 10px 8px;
    border: none;
    border-radius: 0;
    box-shadow: none;
    border-bottom: 1px solid #DDD;
    font-size: 120%;
    outline: none;
    cursor: text;
    resize: none;
    background-color: transparent; }
    .material textarea::-webkit-input-placeholder {
      transition: color 300ms ease; }
    .material textarea:not(:focus)::-webkit-input-placeholder {
      color: transparent; }
  .material input {
    box-sizing: border-box;
    width: 100%;
    padding: 12px 10px 8px;
    border: none;
    border-radius: 0;
    box-shadow: none;
    border-bottom: 1px solid #DDD;
    font-size: 120%;
    outline: none;
    cursor: text;
    background-color: transparent; }
    .material input::-webkit-input-placeholder {
      transition: color 300ms ease; }
    .material input:not(:focus)::-webkit-input-placeholder {
      color: transparent; }
  .material hr {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 2px;
    border: none;
    background-color: #fcd505;
    font-size: 1px;
    will-change: transform, visibility;
    transition: all 200ms ease-out;
    transform: scaleX(0);
    visibility: hidden;
    z-index: 10; }
  .material input:focus ~ hr {
    transform: scaleX(1);
    visibility: visible; }
  .material textarea:focus ~ hr {
    transform: scaleX(1);
    visibility: visible; }
  .material label {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 120%;
    color: #607D8B;
    transform-origin: 0 -150%;
    transition: transform 300ms ease;
    pointer-events: none;
    font-family: "ProximaNova"; }
  .material input:focus ~ label,
  .material input:valid ~ label,
  .material input.active {
    transform: scale(0.6); }
  .material textarea:focus ~ label,
  .material textarea:valid ~ label,
  .material textarea.active {
    transform: scale(0.6); }

