/*----------------------------------*    Header
\*----------------------------------*/
.header {
  z-index: 997;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: white;
  border-bottom: solid 1px #d5d5d5;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  -ms-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  -o-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75); }
  .header img {
    height: 50px; }
  .header__links {
    z-index: 998;
    font-size: 18px;
    height: 70px; }
    .header__links a {
      padding: 0 15px;
      -webkit-transition: 200ms;
      -moz-transition: 200ms;
      -ms-transition: 200ms;
      -o-transition: 200ms;
      transition: 200ms; }
      .header__links a:hover, .header__links a.active {
        color: #fcd505; }
    @media only screen and (max-width: 991px) {
      .header__links {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fcd505;
        flex-direction: column;
        -webkit-flex-direction: column;
        justify-content: center;
        align-items: center;
        -webkit-align-items: center;
        visibility: hidden;
        opacity: 0;
        -webkit-transition: 200ms;
        -moz-transition: 200ms;
        -ms-transition: 200ms;
        -o-transition: 200ms;
        transition: 200ms; }
        .header__links a {
          color: #000;
          text-align: center;
          height: auto;
          font-size: 28px;
          padding: 20px 10px; }
          .header__links a:hover {
            color: #000; }
        .header__links.open {
          visibility: visible;
          opacity: 1; } }
  .header__btn {
    z-index: 999;
    position: relative;
    width: 25px;
    height: 30px;
    cursor: pointer;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg); }
    @media only screen and (min-width: 992px) {
      .header__btn {
        display: none; } }
    .header__btn span {
      position: absolute;
      display: block;
      width: 50%;
      height: 3px;
      opacity: 1;
      background: #000;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: 0.25s ease-in-out;
      -moz-transition: 0.25s ease-in-out;
      -ms-transition: 0.25s ease-in-out;
      -o-transition: 0.25s ease-in-out;
      transition: 0.25s ease-in-out; }
      .header__btn span:nth-child(even) {
        left: 50%;
        border-radius: 0 9px 9px 0; }
      .header__btn span:nth-child(odd) {
        left: 0;
        border-radius: 9px 0 0 9px; }
      .header__btn span:nth-child(1), .header__btn span:nth-child(2) {
        top: 5px; }
      .header__btn span:nth-child(3), .header__btn span:nth-child(4) {
        top: 13px; }
      .header__btn span:nth-child(5), .header__btn span:nth-child(6) {
        top: 21px; }
    .header__btn.open span {
      background-color: #000; }
      .header__btn.open span:nth-child(1), .header__btn.open span:nth-child(6) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg); }
      .header__btn.open span:nth-child(2), .header__btn.open span:nth-child(5) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg); }
      .header__btn.open span:nth-child(1) {
        top: 9px;
        left: 2.5px; }
      .header__btn.open span:nth-child(2) {
        top: 9px;
        left: calc(50% - 2.5px); }
      .header__btn.open span:nth-child(3) {
        left: -50%;
        opacity: 0; }
      .header__btn.open span:nth-child(4) {
        left: 100%;
        opacity: 0; }
      .header__btn.open span:nth-child(5) {
        top: 17px;
        left: 2.5px; }
      .header__btn.open span:nth-child(6) {
        top: 17px;
        left: calc(50% - 2.5px); }

