/*----------------------------------*\
    Table
\*----------------------------------*/
.table {
    overflow-x: auto;
    overflow-y: hidden;
    @include box-shadow(0px 2px 5px 0px rgba(0,0,0,0.3));
    table.responsive{
        width: 100%;
        max-width: 100%;
        border-spacing: 0;
        border-collapse: collapse;

        thead{
            border: solid 1px $color-yellow;
        }

        td,
        th {
            vertical-align: middle;

            text-align: center;
        }
        td{
            border: solid 1px #d5d5d5;
            padding: 5px;
            padding-top: 8px;
        }
        th{
            padding: 3px;
            font-size: 22px;
            background-color: $color-yellow;
        }
    }
}
