/*----------------------------------*\
    Buttons
\*----------------------------------*/
.btn__main{
  color: $color-yellow;
  background-color: #fff;
  border: 1px solid #d5d5d5;
  padding: 0 25px;
  height: 46px;
  border-radius: 23px;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.btn__main:hover{
  background-color: #f6f6f6;
}